import React from 'react';
import { css } from '@emotion/core';
import { colors, styles, viewports } from '../../utils/variables';
import { CopyHeading, BodyText } from '../styled/Typography';
import TextInput from '../inputs/TextInput';
import SubmitButton from '../styled/SubmitButton';

const PartnerContact = ({ heading, copy }) => {
  return (
    <form css={baseCSS}>
      <CopyHeading>{heading}</CopyHeading>
      <BodyText>{copy}</BodyText>
      <div css={formCSS}>
        <TextInput placeholder="Enter email" name="email" style={inputCSS} />
        <SubmitButton>SUBMIT</SubmitButton>
      </div>
    </form>
  );
};

export default PartnerContact;

const baseCSS = css`
  border: 2px ${colors.black} solid;
  box-shadow: ${styles.boxShadow};
  margin: 0 auto;
  padding: 1.25rem;

  @media ${viewports.medium} {
    padding: 1.125rem 6.25rem 2.375rem;
    width: 66.67%;
  }
`;

const formCSS = css`
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media ${viewports.medium} {
    flex-direction: row;
  }
`;

const inputCSS = css`
  margin-bottom: 0.875rem;

  @media ${viewports.medium} {
    margin-bottom: 0;
    width: 25rem;
  }
`;
